import RemainingTime from "./RemainingTime";
import { isExpiredDate } from "../../cards/Utils";

export const TrialExpirationDateCard = ({ licenseExpirationDate }) => {
  const PATTERN_MSG_SAME_EXPIRATION_DATE =
    "Katalon Studio Enterprise and Runtime Engine trial ";

  let isTrialExpired =
    licenseExpirationDate != null && isExpiredDate(licenseExpirationDate);
  let msg = "";
  if (isTrialExpired) {
    msg = PATTERN_MSG_SAME_EXPIRATION_DATE + `expired on`;
  } else {
    msg = PATTERN_MSG_SAME_EXPIRATION_DATE + `will expire on`;
  }
  
  return (
      <RemainingTime text={msg} time={licenseExpirationDate} />
  );
};
