import React from "react";
import { useSearchParams } from 'react-router-dom';
import StartPageCase1 from './StartPageCase1';
import StartPageCase2 from './StartPageCase2';
import StartPageCase3 from './StartPageCase3';

export const StartPageLayout = () => {
  const [search, setSearch] = useSearchParams();
  
  const id = search.get('id')

  if (id === '1') {
    return <StartPageCase1 />
  } else if (id === '2') {
    return <StartPageCase2 />
  } else if (id === '3') {
    return <StartPageCase3 />
  }

  return <StartPageCase3 />
}