import {
  LinearProgress,
  ThemeProvider,
  createTheme,
  makeStyles,
} from "@material-ui/core";
const theme = createTheme({
  palette: {
    primary: {
      main: "#4BA187",
      light: "#DBDDE5",
      dark: "#DBDDE5",
    },
  },
});
const styles = makeStyles(() => ({
  linearProgress: {
    height: "10px",
    borderRadius: "8px 8px 0px 0px ",
    backgroundColor: "#DBDDE5",
  },
}));
export default function CustomProgessBar({value}) {
  const classes = styles();
  return (
    <ThemeProvider theme={theme}>
      <LinearProgress
        value={value}
        variant="determinate"
        color="primary"
        className={classes.linearProgress}
      />
    </ThemeProvider>
  );
}
