import LicenseType from "../../../../variables/LicenseType";
import { EnterpriseExpirationDateCard } from "./license-info/EnterpriseExpirationDateCard";
import { TrialExpirationDateCard } from "./license-info/TrialExpirationDateCard";

export const LicenseInformation = ({
  enterpriseExpirationDate,
  licenseType,
  trialExpirationDate,
}) => {
  return (
    <>
      {licenseType === LicenseType.Enterprise ? (
        <EnterpriseExpirationDateCard
          licenseExpirationDate={enterpriseExpirationDate}
        />
      ) : (
        <TrialExpirationDateCard licenseExpirationDate={trialExpirationDate} />
      )}
    </>
  );
};
