import { Grid, Typography, makeStyles } from "@material-ui/core";
import KatalonStudio from "./KatalonStudio";
import KatalonPlatform from "./KatalonPlatform";
import KSService from "../../../../services/KSService";
import KatalonTour from "../../../../variables/KatalonTour";
import KatalonTours from "../../../../variables/KatalonTours";
import React, { useEffect } from "react";
const styles = makeStyles(() => ({
  label: {
    color: "#727993",
    fontFamily: "Inter",
    fontWeight: "600",
    paddingBottom: "10px",
    fontSize: "12px",
    paddingTop: "10px",
  },
  walkthoughs: {
    justifyContent: "space-between",
  },
  walkthoughsItem: {
    width: "100%",
  },
}));
export default function WalkThroughs() {
  const classes = styles();
  const [studioProgressValue, setStudioProgressValue] = React.useState(0);
  const [platformProgressValue, setPlatformProgressValue] = React.useState(0);

  var isHidden = false;
  if (platformProgressValue === 100 && studioProgressValue === 100) {
    isHidden = true;
  }
  useEffect(() => {
    fetchKataloStudioWalkthrough();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  async function fetchKataloStudioWalkthrough() {
    let data = await KSService.fetchStudioWalkthrough();
    let tourType = data.tourType;
    if (tourType === undefined || tourType == null) {
      tourType = KatalonTour.WEB_TESTING;
    }
    let tours = KatalonTours[tourType];
    let completeTour = data.completedTours;
    if (completeTour !== undefined) {
      let i = 0;
      tours.forEach((t) => {
        if (completeTour.includes(t.id)) {
          i++;
        }
      });
      setStudioProgressValue(Math.round((i / tours.length) * 100));
    }
  }
  return (
    <>
      <Grid container className={classes.root}>
        <Typography className={classes.label}>WALKTHROUGHS</Typography>
        <Grid container className={classes.walkthoughs} spacing={5}>
          <Grid item xs={6} className={classes.walkthoughsItem}>
            <KatalonStudio progressValue={studioProgressValue} isHidden={isHidden}/>
          </Grid>
          <Grid item xs={6} className={classes.walkthoughsItem}>
            <KatalonPlatform progressValue={platformProgressValue} isHidden={isHidden} />
          </Grid>
        </Grid>
      </Grid>
    </>
  );
}
