import { Grid, Typography, makeStyles } from "@material-ui/core";
import { Scrollbars } from "react-custom-scrollbars";
import StartPageQuickLink from "./StartPageQuickLink";
import { StartHeader } from "./StartHeader";
import { QuickStartCheckList } from "../../quick-start/components/QuickStartCheckList";

const styles = makeStyles(() => ({
  root: {
    width: "100%",
    height: "100%",
    margin: 0,
    padding: 40,
    minWidth: "1000px",
  },
  header: {
    animation: "fadeIn 1s ease-in-out",
  },
  startPageContent: {},
  quickStartLabel: {
    color: "#727993",
    marginBottom: 12,
    fontSize: 12,
    height: "15px",
    fontFamily: "Inter",
    fontWeight: "Bold",
    minWidth: "200px",
  },
  checkList: {
    padding: "10px",
    marginTop: "40px",
    marginBottom: "30px",
  },
  checkListHeader: {
    marginBottom: "20px",
  },
  macOScheckListContent: {
    padding: 20,
    paddingRight: 40,
    zoom: "85%",
  },
  windowscheckListContent: {
    padding: 20,
    paddingRight: 40,
    zoom: "120%",
  },
}));

export default function StartPageCase1() {
  const classes = styles();

  const isWindows = navigator.platform.includes('Win');

  return (
    <Scrollbars
      autoHide
      hideTracksWhenNotNeeded
      style={{ width: "100%" }}
      autoHeightMin="100%"
      autoHeightMax="100%"
    >
      <Grid
        container
        direction="column"
        wrap="nowrap"
        spacing={2}
        className={classes.root}
      >
        <Grid item className={classes.header}>
          <StartHeader />
        </Grid>

        <Grid className={classes.startPageContent}>
          <Grid item className={classes.checkList}>
            <Grid item className={classes.checkListHeader}>
              <Typography className={classes.quickStartLabel}>
                QUICK START
              </Typography>
            </Grid>
            <Grid item className={isWindows ? classes.windowscheckListContent : classes.macOScheckListContent}>
              <QuickStartCheckList />
            </Grid>
          </Grid>
          <Grid item>
            <StartPageQuickLink location={"row"} />
          </Grid>
        </Grid>
      </Grid>
    </Scrollbars>
  );
}
