import { Typography, makeStyles } from "@material-ui/core";
import { isExpiredDate } from "../../cards/Utils";

const styles = makeStyles({
  typo: {
    fontStyle: "normal",
    fontFamily: "Inter !important",
    color: "#000000",
    fontWeight: 400,
    fontSize: 13,
  },
  ul: {
    marginTop: 0,
    marginBottom: 0,
  },
  li: {
    marginTop: 0,
  },
  expiredClazz: {
    color: "red",
  },
});
export const EnterpriseExpirationDateCard = ({ licenseExpirationDate }) => {
  const classes = styles();
  const PATTERN_MSG_FOR_KSE_EXPIRATION = "Katalon Studio Enterprise ";
  const PATTERN_MSG_SAME_EXPIRATION_DATE =
    PATTERN_MSG_FOR_KSE_EXPIRATION + "and Runtime Engine ";
  const EXPIRED_ON = "expired on ";
  const WILL_EXPIRE_ON = "will expire on ";

  function checkSameExpirationDate() {
    if (isMissingKRELicense) {
      return true;
    }
    let kse = new Date(licenseExpirationDate.KSE);
    let kre = new Date(licenseExpirationDate.KRE);
    return kse.getTime() === kre.getTime();
  }

  let isMissingKRELicense = licenseExpirationDate.KRE === undefined;
  let isTheSameDate = checkSameExpirationDate();

  function SameExpirationDate() {
    let message = "";
    let isExpiredBothLicense = isExpiredDate(licenseExpirationDate.KSE);
    let verb = isExpiredBothLicense ? EXPIRED_ON : WILL_EXPIRE_ON;
    if (isMissingKRELicense) {
      message = PATTERN_MSG_FOR_KSE_EXPIRATION + verb;
    } else {
      if (isTheSameDate) {
        message = PATTERN_MSG_SAME_EXPIRATION_DATE + verb;
      }
    }
    let clazzName = isExpiredBothLicense ? classes.expiredClazz : "";
    return (
      <Typography variant="caption" className={classes.typo}>
        {`${message} `}
        <b className={clazzName}>{licenseExpirationDate.KSE}</b>
      </Typography>
    );
  }
  function getBoldDateTag(date) {
    let clazzName = isExpiredDate(date) ? classes.expiredClazz : "";
    return <b className={clazzName}>{date}</b>;
  }

  const DifferentExpirationDate = () => {
    return (
      <Typography variant="caption" className={classes.typo}>
        Your licenses expiration dates:
        <br />
        <ul className={classes.ul}>
          <li className={classes.li}>
            {"Katalon Studio Enterprise: "}
            {getBoldDateTag(licenseExpirationDate.KSE)}
          </li>
          <li className={classes.li}>
            {"Katalon Runtime Engine: "}
            {getBoldDateTag(licenseExpirationDate.KRE)}
          </li>
        </ul>
      </Typography>
    );
  };
  let widthDiv = "21rem";
  if (isTheSameDate) {
    widthDiv = isMissingKRELicense ? "15rem" : "20rem";
  }

  return (
    <div style={{ width: widthDiv }}>
      {isTheSameDate ? (
        <SameExpirationDate />
      ) : (
        <DifferentExpirationDate
          licenseExpirationDate={licenseExpirationDate}
        />
      )}
    </div>
  );
};
