import { Grid, Typography, makeStyles } from "@material-ui/core";
import backgroundImage from "../../../../../assets/img/walkthroughs/platform.png";
import CustomProgessBar from "./CustomProgressBar";
import LaunchIcon from "@material-ui/icons/Launch";
import KSActionService from "../../../../services/KSActionService";
const styles = makeStyles(() => ({
  platform: {
    backgroundImage: `url(${backgroundImage})`,
    backgroundPosition: "bottom right",
    height: "calc(100% - 10px)",
    backgroundRepeat: "no-repeat",
    backgroundSize: "calc((100% - 10px)*0.4)",
    background: "#FAFAFA",
    borderRadius: "0px 0px 8px 8px",
    paddingBottom: "10px",
  },
  contentHeader: {
    paddingTop: "15px",
  },
  content: {
    paddingLeft: "20px",
    paddingRight: "calc(20%)",
  },
  typoHeader: {
    paddingBottom: "8px",
    fontSize: "16px",
    fontFamily: "Inter",
    fontWeight: "600",
  },
  typoContent: {
    fontSize: "12px",
    fontFamily: "Inter",
    fontWeight: "400",
    color: "#7B8197",
  },
  icon: {
    height: "100%",
    display: "flex",
    alignItems: "flex-start",
    marginRight: "20px",
  },
}));
export default function KatalonPlatform({ progressValue, isHidden }) {
  const classes = styles();

  async function onClickOpenKatalonPlatform() {
    await KSActionService.openKatalonPlatformWalkthrough();
  }
  return (
    <>
      <CustomProgessBar value={progressValue} />
      <div className={isHidden ? classes.platformDone : classes.platform}>
        <Grid container className={classes.contentHeader}>
          <Grid item className={classes.content} sm>
            <Typography variant="h6" className={classes.typoHeader}>
              Katalon Platform
            </Typography>
            {!isHidden && (
              <Typography variant="body" className={classes.typoContent}>
                {
                  "Learn how to manage quality in an all-in-one test orchestration platform"
                }
              </Typography>
            )}
          </Grid>
          <Grid item>
            <div className={classes.icon}>
              <LaunchIcon
                onClick={() => onClickOpenKatalonPlatform()}
                onMouseOver={({ target }) => {
                  target.style.background = "#DBDDE5";
                  target.style.borderRadius = "8px";
                }}
                onMouseLeave={({ target }) => {
                  target.style.background = "#FAFAFA";
                  target.style.borderRadius = "0";
                }}
              />
            </div>
          </Grid>
        </Grid>
      </div>
    </>
  );
}
