import React from 'react';
import { Grid, makeStyles } from '@material-ui/core';
import QuickLink from './quicklink-components/QuickLink'
import Resources from './quicklink-components/Resources'

const styles = makeStyles(() => ({
  root: {
    width: '100%',
    padding: "10px",
  },
}));

export default function StartPageFooter(layout) {
  const classes = styles();
  return (
    <Grid container direction={layout.location} wrap="nowrap" className={classes.root}>
      <QuickLink/>
      { layout.location === 'column' ? <Grid style={{ marginTop: "15px", marginBottom: "15px" }}></Grid> : <Grid xs={1} sm={1}></Grid> }
      <Resources/>
    </Grid>
  );
}
