/* eslint-disable no-nested-ternary */
/* eslint-disable no-bitwise */
import React, { memo } from "react";
import moment from "moment";
import countdown from "../../../../../utils/countdown/countdown";
import { Typography, makeStyles } from "@material-ui/core";
import { useForceUpdate } from "../../../../../utils/ReactUtils";
const styles = makeStyles({
  typo: {
    fontStyle: "normal",
    fontFamily: "Inter !important",
    color: "#000000",
    fontSize: 13,
    fontWeight: 400,
  },
  expiredClazz: {
    color: "red",
  },
});

export default memo(({ time, text }) => {
  const classes = styles();
  const [timer, setTimer] = React.useState();
  const forceUpdate = useForceUpdate();
  React.useEffect(() => {
    clearInterval(timer);
    setTimer(
      setInterval(() => {
        forceUpdate();
      }, 1000)
    );
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  let clazzName = moment(Date.now()).isBefore(time) ? "" : classes.expiredClazz;
  function getRemainingTime() {
    const timeString = moment(time)
      .countdown(
        Date.now(),
        countdown.DAYS |
          countdown.HOURS |
          countdown.MINUTES |
          countdown.SECONDS,
        1
      )
      .toString();
    let msg = `${moment(time).format("LL")}`;
    let displayText = text;
    if (
      moment(Date.now()).isBefore(time) &&
      moment(Date.now()).isSame(time, "day")
    ) {
      displayText = text.substring(0, text.length - 2) + "in";
      msg = `the next ${timeString}`;
      clazzName = classes.expiredClazz;
    }
    return (
      <Typography variant="caption" className={classes.typo}>
        {`${displayText} `}
        <b className={clazzName}>{time ? `${msg}` : "..."}</b>
      </Typography>
    );
  }

  let intWidth = "17rem";
  if ((text + time).length > 70) {
    intWidth = "20rem";
  }
  return <div style={{ width: intWidth }}>{getRemainingTime()}</div>;
});
