import { Grid, Typography, makeStyles } from "@material-ui/core";
import backgroundImage from "../../../../../assets/img/walkthroughs/studio.png";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import CustomProgessBar from "./CustomProgressBar";
import KSActionService from "../../../../services/KSActionService";

const styles = makeStyles(() => ({
  platform: {
    backgroundImage: `url(${backgroundImage})`,
    backgroundPosition: "bottom right",
    height: "calc(100% - 10px)",
    backgroundRepeat: "no-repeat",
    backgroundSize: "calc((100% - 10px)*0.4)",
    background: "#FAFAFA",
    borderRadius: "0px 0px 8px 8px",
    paddingBottom: "10px",
  },
  platformDone: {
    background: "#FAFAFA",
    paddingBottom: "10px",
    borderRadius: "0px 0px 8px 8px",
  },
  contentHeader: {
    paddingTop: "15px",
  },
  content: {
    paddingLeft: "20px",
    paddingRight: "calc(20%)",
  },
  typoHeader: {
    fontSize: "16px",
    paddingBottom: "8px",
    fontFamily: "Inter",
    fontWeight: "600",
  },
  typoContent: {
    fontSize: "12px",
    fontFamily: "Inter",
    fontWeight: "400",
    color: "#7B8197",
  },
  icon: {
    height: "100%",
    display: "flex",
    alignItems: "flex-start",
    marginRight: "20px",
  },
}));
export default function KatalonStudio({ progressValue, isHidden }) {
  const classes = styles();

  async function onClickOpenKatalonStudio() {
    await KSActionService.openKatalonStudioWalkthrough();
  }

  return (
    <>
      <CustomProgessBar value={progressValue} />
      <div className={isHidden ? classes.platformDone : classes.platform}>
        <Grid container className={classes.contentHeader}>
          <Grid item sm className={classes.content}>
            <Typography variant="h6" className={classes.typoHeader}>
              Katalon Studio
            </Typography>
            {!isHidden && (
              <Typography variant="body" className={classes.typoContent}>
                {
                  "Balances low-code ease with full-code flexibility, so adoption is fast"
                }
              </Typography>
            )}
          </Grid>
          <Grid item>
            <div className={classes.icon}>
              <ArrowForwardIcon
                onClick={() => onClickOpenKatalonStudio()}
                onMouseOver={({ target }) => {
                  target.style.background = "#DBDDE5";
                  target.style.borderRadius = "8px";
                }}
                onMouseLeave={({ target }) => {
                  target.style.background = "#FAFAFA";
                  target.style.borderRadius = "0";
                }}
              />
            </div>
          </Grid>
        </Grid>
      </div>
    </>
  );
}
